.background {
  position: absolute;
  top: 120vh;
  left: 0;
  bottom: auto;
  width: 126vw;
  height: 180vh;
  z-index: -1;
  transform: scale(0.8, 0.8);
  overflow-x: hidden;
  overflow-y: visible;
  margin: 0 -43px;

  @media screen and (min-width: $screen-tablet) {
    top: 160vh;
    transform: scale(1.2, 1.2);
    width: 100vw;
    height: 100vh;
    margin: auto;
  }

  @media screen and (min-width: 1350px) {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    transform: scale(1.0, 1.0);
  }

}
.blob {
  position: absolute;

  &--blue {
    bottom: -500px;
    right: -350px;
    transform: rotate(0deg);

    svg {
    	width: 1200px;
    	height: 1200px;
    }

    path {
      animation: morpheus 35s linear infinite;
    }
  }

  &--orange {
    top: 25px;
    right: -200px;
    transform: rotate(0deg);

    svg {
    	width: 550px;
    	height: 550px;
    }

    path {
      animation: morpheus2 25s linear  infinite;
    }
  }

  &--purple {
    top: -500px;
    right: -350px;
    transform: rotate(360deg);

    svg {
    	width: 1200px;
    	height: 1200px;
    }

    path {
      animation: morpheus3 30s linear  infinite;
    }
  }
}

